@import url("https://fonts.googleapis.com/css?family=Nunito");
@import url("https://fonts.googleapis.com/css?family=Nunito+Sans");
/*button-variant($background, $border, $hover-background: darken($background, 7.5%), $hover-border: darken($border, 10%), $active-background: darken($background, 10%), $active-border: darken($border, 12.5%))*/
.btn-wick-warning {
  background: #E6460E;
  border: #E6460E;
  color: #000; }

.btn-wick-warning:hover {
  background: #c23b0c;
  border: #c23b0c; }

.btn-wick-warning:active {
  background: #aa340a;
  border: #aa340a; }

.btn-wick-accept {
  background: #01C094;
  border: #01C094;
  color: #000; }

.btn-wick-accept:hover {
  background: #019a77;
  border: #019a77; }

.btn-wick-accept:active {
  background: #018163;
  border: #018163; }

:export {
  editorCanvasBorder: #6A6A6A; }

.canvas-transforms-widget {
  position: absolute;
  right: 0px;
  bottom: 0px;
  height: 40px;
  margin-right: 10px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  background-color: #191919;
  border-radius: 4px; }

#canvas-zoom-options-container {
  display: flex;
  flex-direction: row;
  align-items: center; }

#zoom-tool-container {
  width: 40px;
  height: 40px; }

.canvas-transform-item {
  padding: 2px; }

.canvas-transform-button {
  width: 40px;
  height: 40px;
  padding: 2px; }

.thin-transform-button {
  width: calc(40px / 2); }

.play-button {
  width: calc(40px * .9);
  height: calc(40px * .9); }

.play-button-container {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center; }

.transforms-container {
  display: flex;
  flex-direction: row;
  align-items: center; }
